export function formatMonths(monthCount: string) {
  const months = Number(monthCount) % 12;
  const years = Math.floor(Number(monthCount) / 12);
  let out = '';
  if (years > 0) {
    out += years + ' year';
    if (years > 1) {
      out += 's';
    }
  }

  if (months > 0) {
    if (years > 0) {
      out += ', ';
    }
    out += months + ' month';
    if (months > 1) {
      out += 's';
    }
  }
  return out;
}
